import Store from '@/store'

const authCheck = ((to, from, next) => {
  const getIsAuthenticated = Store.getters.getIsAuthenticated
  if (!getIsAuthenticated) {
    if (Store.getters.getUserId) {
      next('/')
    } else { // 로그인 X
      next()
    }
  } else { // 로그인
    next('/')
  }
})

const addRoute =
  {
    path: '/',
    name: 'MyPageMain',
    meta: {layout: 'NoneLayout'},
    component: () => import(/* webpackChunkName: "MyPageMain" */ '@/views/myPage/Main'),
    children: [
      {
        path: 'mypage',
        name: 'MyPage',
        prop: true,
        meta: {layout: 'DefaultLayout'},
        component: () => import(/* webpackChunkName: "myPage" */ '@/views/myPage/MyPage.vue'),
      },
      {
        path: 'profile',
        name: 'ProfileEdit',
        prop: true,
        meta: {layout: 'DefaultLayout'},
        component: () => import(/* webpackChunkName: "profile" */ '@/views/myPage/ProfileEdit.vue'),
      },
      {
        path: 'user-info',
        name: 'UserInfo',
        prop: true,
        meta: {layout: 'DefaultLayout'},
        component: () => import(/* webpackChunkName: "user-info" */ '@/views/myPage/UserInfo.vue'),
      },
      {
        path: 'login',
        name: 'Login',
        prop: true,
        meta: {layout: 'HeaderLayout'},
        beforeEnter: authCheck,
        component: () => import(/* webpackChunkName: "login" */ '@/views/myPage/Login'),
      },
      {
        path: 'join',
        name: 'Join',
        prop: true,
        meta: {layout: 'HeaderLayout'},
        beforeEnter: authCheck,
        component: () => import(/* webpackChunkName: "join2" */ '@/views/myPage/Join.vue'),
      },
      {
        path: 'find',
        name: 'Find',
        prop: true,
        meta: {layout: 'HeaderLayout'},
        beforeEnter: authCheck,
        component: () => import(/* webpackChunkName: "find" */ '@/views/myPage/Find'),
        children: [
          {
            path: 'id',
            name: 'FindId',
            prop: true,
            meta: {layout: 'HeaderLayout'},
            component: () => import(/* webpackChunkName: "step3" */ '@/views/myPage/FindId.vue'),
          },
          {
            path: 'pw',
            name: 'FindPw',
            prop: true,
            meta: {layout: 'HeaderLayout'},
            component: () => import(/* webpackChunkName: "pw" */ '@/views/myPage/FindPw.vue'),
          },
        ]
      },
    ],
  }

export default addRoute