const addRoute =
  {
    path: '/',
    name: 'challengeMain',
    component: () => import(/* webpackChunkName: "challengeMain" */ '@/views/challenge/Main'),
    children: [
      {
        path: 'challenge',
        name: 'ChallengeList',
        prop: true,
        component: () => import(/* webpackChunkName: "challengeList" */ '@/views/challenge/List'),
      },
      {
        path: 'challenge/write',
        name: 'ChallengeWrite',
        prop: true,
        component: () => import(/* webpackChunkName: "challengeWrite" */ '@/views/challenge/Write.vue'),
      },
      {
        path: 'challenge/edit',
        name: 'challengeEdit',
        prop: true,
        component: () => import(/* webpackChunkName: "challengeEdit" */ '@/views/challenge/Edit.vue'),
      },
      {
        path: 'challenge/view',
        name: 'challengeView',
        prop: true,
        component: () => import(/* webpackChunkName: "challengeView" */ '@/views/challenge/View.vue'),
      },
    ]
  }
export default addRoute