
const addRoute =
  {
    path: '/',
    name: 'magazineMain',
    component: () => import(/* webpackChunkName: "magazineMain" */ '@/views/magazine/Main'),
    children: [
      {
        path: 'magazine',
        name: 'MagazineList',
        prop: true,
        component: () => import(/* webpackChunkName: "magazineList" */ '@/views/magazine/List'),
      },
      {
        path: 'magazine/detail',
        name: 'MagazineDetail',
        prop: true,
        meta: {layout: 'HeaderLayout'},
        component: () => import(/* webpackChunkName: "magazineDetail" */ '@/views/magazine/Detail'),
      },
      {
        path: 'magazine/detail2',
        name: 'MagazineDetail2',
        prop: true,
        meta: {layout: 'HeaderLayout'},
        component: () => import(/* webpackChunkName: "magazineDetail2" */ '@/views/magazine/Detail2'),
      },
    ]
  }
export default addRoute